document.addEventListener('DOMContentLoaded', function() {
    iniciarApp();
});

/* Generando galeria */
function iniciarApp() {
    navegacionFija();
    crearGaleria();
    scrollNav();
}

/* Funcion para que el navegador de la web se quede en la parte superior */
function navegacionFija() {
    const barra = document.querySelector('.header');/* Seleccionamos el header */
    const sobreFestival = document.querySelector('.sobre-festival'); /* Selleccionamos la posicion donde empezara a mostrarse */
    const body = document.querySelector('body');/* Para eliminar el salto que hace al añadirse la barra */

    /* Para saber en que posicion se encuentra el scroll */
    window.addEventListener('scroll', function() {

        if(sobreFestival.getBoundingClientRect().top < 0) {
            barra.classList.add('fijo');/* Añadimos una clase a la barra */
            body.classList.add('body-scroll');
        } else {
            barra.classList.remove('fijo');/* Eliminamos la clase */
            body.classList.remove('body-scroll');
        }
    });
}

function scrollNav() { /* Efecto smooth scroll */
    /* Selleccionamos todos los enlaces */
    const enlaces = document.querySelectorAll('.navegacion-principal a');
    enlaces.forEach( enlace => {
        enlace.addEventListener('click', function(e) {
            e.preventDefault();/* Prevenimos que no use el scroll por los id del Html */

            /* Creamos el scroll */
            const seccionScroll = e.target.attributes.href.value;
            const seccion = document.querySelector(seccionScroll);
            seccion.scrollIntoView({ behavior: "smooth"});
        });
    });
}

function crearGaleria() {
    const galeria = document.querySelector('.galeria-imagenes');

    for(let i = 1; i <= 12; i++) { /* Iteramos sobre la carpeta para la galeria */
        const imagen = document.createElement('picture');
        imagen.innerHTML=`
        <source srcset="build/img/thumb/${i}.avif" type="image/avif">
        <source srcset="build/img/thumb/${i}.webp" type="image/webp">
        <img loading="lazy" src="build/img/thumb/${i}.jpg" alt="imagen galería">
        `;
        imagen.onclick = function() {
            mostrarImagen(i);
        }


        galeria.appendChild(imagen);
    }
} /* Fin galeria */


/* Mostrando galeria al hacer click */
function mostrarImagen(id) {
    const imagen = document.createElement('picture');
    imagen.innerHTML=`
    <source srcset="build/img/grande/${id}.avif" type="image/avif">
    <source srcset="build/img/grande/${id}.webp" type="image/webp">
    <img loading="lazy" src="build/img/grande/${id}.jpg" alt="imagen galería">
    `;

    // Creando overlay con la imagen
    const overlay = document.createElement('DIV');
    overlay.appendChild(imagen);
    overlay.classList.add('overlay');
    overlay.onclick = function() { /* Para poder cerrar la imagen clicando en ella */
        const body = document.querySelector('body');
        body.classList.remove('fijar-body');
        overlay.remove();
    }

    // Boton para cerrar el modal
    const cerrarModal = document.createElement('P');
    /* Agrega una x para cerrar la imagen */
/*     cerrarModal.textContent = 'X';
    cerrarModal.classList.add('btn-cerrar'); */
    cerrarModal.onclick = function() {
        const body = document.querySelector('body');
        body.classList.remove('fijar-body');/* Eliminamos la clase par apoder hacer scroll despues de cerrarla */
        overlay.remove();
    }
    overlay.appendChild(cerrarModal);

    // Añadimos a Html
    const body = document.querySelector('body');
    body.appendChild(overlay);
    body.classList.add('fijar-body');
}